<template>
  <div>
    <div>vueKeepAliveBase</div>max:
    <input v-model="max" />
    <button 
      @click="()=>{
      this.view=this.view=='Eg'?'Eg1':'Eg'
      }">component - {{view}}</button>
    <button
      @click="()=>{
      this.useKeepAlive=!this.useKeepAlive
      }"
    >useKeepAlive - {{useKeepAlive}}</button>
    <keep-alive :max="max">
      <component v-if="useKeepAlive" :is="view"></component>
    </keep-alive>
    <component v-if="!useKeepAlive" :is="view"></component>
  </div>
</template>

<script>
import Eg from "./eg";
import Eg1 from "./eg1";

export default {
  name: "vueKeepAliveBase",
  components: {
    Eg,
    Eg1
  },
  data() {
    return {
      view: "Eg",
      useKeepAlive: true,
      max: 1
    };
  },
  mounted() {
    console.log("vueKeepAliveBase");
  }
};
</script>
